	      "speakerId": 4,
	      "anchor": "Tomasz-Lakomy-24-12-30",
	      "description": ["It's time to leave the 2D world of a browser window and venture into the world of enhanced reality JavaScript applications. During the talk, we'll see how to make great VR and AR experiences using just your browser and a bit of React, with live demos included, so we'll get to see them in action!"],
